import React from "react";

function Timezone(){

    setInterval(curtime, 1000)

    var [time,updatetime] = React.useState(new Date());

    function curtime(){
        updatetime(new Date());
    }

    var timezone = time.getHours()>12?"PM":"AM";

    // var [change,setbool] = React.useState(false);

    // function changestyle(){
    //     setbool(true);
    // }

    // function revertchange(){
    //     setbool(false);
    // }

    return(
        <div className="timezone">
            <h1>{time.getHours()}:{time.getMinutes()} {timezone} </h1>
            {/* <button 
            style={{backgroundColor:change && "black", color:change && "wheat"}} 
            onClick={curtime} 
            onMouseOver = {changestyle} 
            onMouseOut = {revertchange} >Get Time</button> */}
        </div>
    )
}

export default Timezone;